<template>
  <div class="homepage">
    <div class="head">
      <h1>{{ title }}</h1>

      <div class="head-line">
        <p>Diversity is our strength.</p>
        <p>Finding out your own Team Role preferences as well as those of your colleagues will enable you to ensure tasks are allocated to the appropriate team members and this will result in more a more effective and productive team.</p>
        <p>Team Role is all about how you behave and interact with others. Anything that causes your behavior to change can in turn have an impact on your Team Role preferences.</p>
        <p>Let's make the world a better place.</p>
      </div>

      <div id="belbin-matrix">
        <img class="role_icon" v-for="team_role in team_roles" :key="'icon_' + team_role.id" :alt="team_role.name" :src="team_role.src" :class="{backstage: team_role.backstage}">
      </div>
    </div>

    <div class="content">
      <form id="role_questions" action="https://formspree.io/jq@angelabor.com" method="POST">
        <div class="role_question" v-for="(question, index) in shuffled_role_questions" :key="index">
          <input :id="'question_' + index" type="checkbox" v-model=question.checked>
          <label :for="'question_' + index" class="role_question_checkbox" v-on:click="tally(question.role)"></label>
          <label class="role_question_text">{{question.text}}</label>
          <br>
          <br>
        </div>

        <div id="test-result">
          <h2>Your Result</h2>
          <div id="belbin-matrix">
            <img class="role_icon" v-for="team_role in team_roles" :key="'icon_' + team_role.id" :alt="team_role.name" :src="team_role.src" :class="{backstage: team_role.backstage}">
          </div>
          <div class="test-result-text">
            <p>Suggestions for you:</p>
            <div v-for="team_role in team_roles" v-if="!team_role.backstage" :key="'comment_' + team_role.id">
              <textarea :name="'Team Role: ' + team_role.name" v-model="team_role.comment" readonly="readonly" rows="7"></textarea>
            </div>
            <div v-for="team_role in team_roles" :key="'result_' + team_role.id">
              <input type="hidden" :name="team_role.name" :value="(!team_role.backstage ? 'Pass' : 'Miss') + ', Score: ' + team_role.tally + '/4'">
            </div>
          </div>
        </div>

        <input type="text" name="_gotcha" style="display:none" />
        <input type="text" name="_cc" placeholder="What is your email?" />
        <input type="hidden" name="_subject" value="9 Team Roles" />
        <input class="button" type="submit" value="Send">
        <input type="hidden" name="_next" value="http://antslabor.com" />
      </form>

    </div>
  </div>
</template>

<script>
export default {
  name: 'TeamRoles',
  methods: {
    tally: function (role) {
      return this.role_questions.filter(question => (question.role === role && question.checked)).length
    },
    show: function (role, tally) {
      var roleInfo = this.team_roles.find(teamRole => teamRole.id === role)
      roleInfo.tally = tally
      roleInfo.backstage = tally < 3
    },
    get_comment: function (role, backstage) {
      var comment = this.comments.filter(comment => (comment.role === role))
      return backstage ? comment.miss : comment.have
    },
    shuffle: function (array) {
      var currentIndex = array.length
      var temporaryValue
      var randomIndex

      // While there remain elements to shuffle...
      while (currentIndex !== 0) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex)
        currentIndex -= 1

        // And swap it with the current element.
        temporaryValue = array[currentIndex]
        array[currentIndex] = array[randomIndex]
        array[randomIndex] = temporaryValue
      }

      return array
    }
  },
  computed: {
    shuffled_role_questions: function () {
      return this.shuffle(this.role_questions)
    },
    tally_innovator: function () {
      return this.tally('innovator')
    },
    tally_explorer: function () {
      return this.tally('explorer')
    },
    tally_analyst: function () {
      return this.tally('analyst')
    },
    tally_coordinator: function () {
      return this.tally('coordinator')
    },
    tally_implementer: function () {
      return this.tally('implementer')
    },
    tally_expert: function () {
      return this.tally('expert')
    },
    tally_driver: function () {
      return this.tally('driver')
    },
    tally_teammate: function () {
      return this.tally('teammate')
    },
    tally_proofreader: function () {
      return this.tally('proofreader')
    }
  },
  watch: {
    tally_innovator: function (tally) {
      this.show('innovator', tally)
    },
    tally_explorer: function (tally) {
      this.show('explorer', tally)
    },
    tally_analyst: function (tally) {
      this.show('analyst', tally)
    },
    tally_coordinator: function (tally) {
      this.show('coordinator', tally)
    },
    tally_implementer: function (tally) {
      this.show('implementer', tally)
    },
    tally_expert: function (tally) {
      this.show('expert', tally)
    },
    tally_driver: function (tally) {
      this.show('driver', tally)
    },
    tally_teammate: function (tally) {
      this.show('teammate', tally)
    },
    tally_proofreader: function (tally) {
      this.show('proofreader', tally)
    }
  },
  data () {
    return {
      title: '9 Belbin Team Roles',
      team_roles: [
        {
          id: 'innovator',
          name: 'Innovator',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Plant.jpg'),
          comment: 'You are the Innovators who generate new ideas and approaches to solve difficult problems. Innovators are absent-minded or forgetful. You thrive on praise but criticism is especially hard for you to deal with. Your ideas are so novel, which can be impractical at times. You may also be poor communicators and can tend to ignore given parameters and constraints.'
        },
        {
          id: 'explorer',
          name: 'Explorer',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Resource-Investigator.jpg'),
          comment: 'You are innovative and curious Explorers. You explore available options, develop contacts, and negotiate for resources on behalf of the team. You are enthusiastic team members, who identify and work with external stakeholders to help the team accomplish its objective. On the downside, you may lose enthusiasm quickly and are often overly optimistic. You might forget to follow up on a lead.'
        },
        {
          id: 'analyst',
          name: 'Analyst',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Monitor-Evaluator.jpg'),
          comment: 'You are the Monitors who are best at analyzing and evaluating ideas that other people (often Innovators) come up with. You are shrewd and objective, and they carefully weigh the pros and cons of all the options before coming to a decision. You are critical thinkers and very strategic in your approach. You are often perceived as detached or unemotional. Sometimes you are poor motivators who react to events rather than instigating them.'
        },

        {
          id: 'coordinator',
          name: 'Coordinator',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Co-ordinator.jpg'),
          comment: 'You are the Chairmans who guide the team to what they perceive are the objectives. They are often excellent listeners and they are naturally able to recognize the value that each team member brings to the table. They are calm and good-natured, and delegate tasks very effectively. You may delegate away too much personal responsibility and may tend to be manipulative.'
        },
        {
          id: 'implementer',
          name: 'Implementer',
          tally: 0,
          backstage: false,
          src: require('../assets/img/RoleImage - Implementer.jpg'),
          comment: 'You are Executives who get things done. You turn the team\'s ideas and concepts into practical actions and plans. You are the person people can count on to get the job done. On the downside, you may be inflexible and can be somewhat resistant to relinquish your plans in favor of positive changes.'
        },
        {
          id: 'expert',
          name: 'Expert',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Specialist.jpg'),
          comment: 'You are the Experts who have specialized knowledge that is needed to get the job done. You pride yourself on your skills and abilities, and you work to maintain your professional status. Your job within the team is to be an expert in the area, and you commit yourself fully to your field of expertise. This may limit your contribution, and lead to a preoccupation with technicalities at the expense of the bigger picture.'
        },

        {
          id: 'driver',
          name: 'Driver',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Shaper.jpg'),
          comment: 'You are the Shapers who challenge the team to improve. You are dynamic and usually extroverted who enjoy stimulating others, questioning norms, and finding the best approaches for solving problems. You are the one who shakes things up to make sure that all possibilities are considered and that the team does not become complacent. You often see obstacles as exciting challenges and you tend to have the courage to push on when others feel like quitting. Your potential weaknesses may be that you are argumentative and that you may offend people\'s feelings.'
        },
        {
          id: 'teammate',
          name: 'Teammate',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Teamworker.jpg'),
          comment: 'You are the Active Listener who provide support and make sure that people within the team are working together effectively. You fill the role of negotiators within the team and they are flexible, diplomatic and perceptive. You tend to be popular people who are very capable in their own right, but who prioritize team cohesion and helping people get along. Your weaknesses may be a tendency to be indecisive and to maintain uncommitted positions during discussions and decision-making.'
        },
        {
          id: 'proofreader',
          name: 'Proofreader',
          tally: 0,
          backstage: true,
          src: require('../assets/img/RoleImage - Complete-Finisher.jpg'),
          comment: 'You are the Completer who see projects getting completed thoroughly. You ensure there have been no errors or omissions and you pay attention to the smallest of details. You are described as perfectionists who are orderly, conscientious and anxious. Most effectively worked at the end of tasks to polish, perfect, and scrutinize the work for errors, subjecting it to the highest standards of quality control. However, you could be accused of taking your perfectionism to extremes. You worry unnecessarily and find it hard to delegate.'
        }
      ],
      role_questions: [
        {checked: false, role: 'innovator', text: 'Are you creative, imaginative, free-thinking? Are you a source of original ideas?'},
        {checked: false, role: 'innovator', text: 'Are you good at solving difficult problems in an unconventional ways?'},
        {checked: false, role: 'innovator', text: 'Are you introverted and prefer to work apart from the team?'},
        {checked: false, role: 'innovator', text: 'Do you tend to ignore less important things?'},
        {checked: false, role: 'explorer', text: 'Are you extrovert, enthusiastic, communicative? Are you a salesperson who is the source of vitality? Do you explore opportunities and develops contacts?'},
        {checked: false, role: 'explorer', text: 'Do you use your inquisitive nature to find ideas to bring back to the team?'},
        {checked: false, role: 'explorer', text: 'Do you tend to be over-optimistic, and can lose interest once the initial enthusiasm has passed? Are you easily diverted?'},
        {checked: false, role: 'explorer', text: 'Are you outgoing and often extroverted, meaning that others are often receptive to you and your ideas?'},
        {checked: false, role: 'analyst', text: 'Are you sober, strategic, discerning, sees all options and judges accurately?'},
        {checked: false, role: 'analyst', text: 'Could you provide a logical eye, making impartial judgments where required and weighs up the team\'s options in a dispassionate way?'},
        {checked: false, role: 'analyst', text: 'Do you provide critique with the expectation of improving performance?'},
        {checked: false, role: 'analyst', text: 'Do you come to decisions slowly?'},
        {checked: false, role: 'coordinator', text: 'Are you a mature, confident, good chairperson? Are you the decision maker and delegator?'},
        {checked: false, role: 'coordinator', text: 'Do you take on the traditional team-leader role?'},
        {checked: false, role: 'coordinator', text: 'Do you identify talent and clarifies goals?'},
        {checked: false, role: 'coordinator', text: 'Do you focus on the team\'s objectives and draw out team members and delegate work appropriately?'},
        {checked: false, role: 'implementer', text: 'Are you disciplined, reliable, conservative and efficient? Do you turn ideas into practical actions and organizes work that needs to be done?'},
        {checked: false, role: 'implementer', text: 'Do you tend to plan a workable strategy and carry it out as efficiently as possible?'},
        {checked: false, role: 'implementer', text: 'Do you tend to resist unproven ideas? Are you somewhat inflexible and slow to respond to new possibilities?'},
        {checked: false, role: 'implementer', text: 'Do you work systematically and efficiently and are very well organized?'},
        {checked: false, role: 'expert', text: 'Are you focused, self-starting and dedicated? Providing knowledge & skills in rare supply?'},
        {checked: false, role: 'expert', text: 'Do you bring in-depth knowledge of a key area to the team?'},
        {checked: false, role: 'expert', text: 'Do you tend to overload people with information?'},
        {checked: false, role: 'expert', text: 'Do you tend to overlook the “big picture”? Do you contribute only on a narrow front and dwell on the technicalities?'},
        {checked: false, role: 'driver', text: 'Are you challenging, dynamic, thrives on pressure? Are you the courage to overcome obstacles, brings the competitive drive to the team?'},
        {checked: false, role: 'driver', text: 'Provides the necessary drive to ensure that the team keeps moving and does not lose focus or momentum?'},
        {checked: false, role: 'driver', text: 'Are you prone to provocation, impatience, and sometimes offend peoples’ feelings? Are you seen as abrasive?'},
        {checked: false, role: 'driver', text: 'Could you risk becoming aggressive and bad humored in your attempts to get things done?'},
        {checked: false, role: 'teammate', text: 'Are you co-operative, mild, perceptive and diplomatic? Are you a good listener who averts friction?'},
        {checked: false, role: 'teammate', text: 'Helps the team to gel, using their versatility to identify the work required and complete it on behalf of the team?'},
        {checked: false, role: 'teammate', text: 'Are you indecisive in crunch situations and tends to avoid confrontation?'},
        {checked: false, role: 'teammate', text: 'Are you hesitant to make unpopular decisions?'},
        {checked: false, role: 'proofreader', text: 'Are you painstakingly conscientious, anxious? Do you seek out errors and omissions?'},
        {checked: false, role: 'proofreader', text: 'Do you always meet deadlines and work to highest standards?'},
        {checked: false, role: 'proofreader', text: 'Are you very concerned with deadlines and will push the team to make sure the job is completed on time?'},
        {checked: false, role: 'proofreader', text: 'Are you a perfectionist?'}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import '../assets/css/checkbox.css';

h1, h2 {
  font-weight: normal;
  text-align: center;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
p {
  font-family: Helvetica, Arial, Sans-Serif;
}
p.highlight {
  font-weight: bold;
}

img.logo {
  filter:drop-shadow(8px 8px 10px gray);
}
img.role_icon {
  height: 100px;
  width: 100px;
  padding-left: 3px;
  padding-right: 3px;
}
img.backstage {
  filter:grayscale(100%) blur(2px);
}

button, input.button {
  border: 2px solid blue;
  border-radius: 25px;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  float: right;
}

input[type=text] {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

textarea {
  width: 80%;
  min-height: 5em;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-family: Helvetica, Arial, Sans-Serif;
  font-size: 16px;
}

#topBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  border: none;
  outline: none;
  background-color: red;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 10px;
}

#topBtn:hover {
  background-color: #555;
}

div.head-line {
  width: 60%;
  text-align: left;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 10px;
}

#belbin-matrix {
  width: 360px;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;

  border: 5px solid #111;
  border-radius:25px;
  -moz-box-shadow: 35px 35px 7px #999999;
  -moz-border-bottom-right-radius: 15px;
  -moz-border-radius: 25px;
  -webkit-border-radius: 25px;
  padding: 20px 20px;
}

div.content {
  padding: 20px 20px;
}

#role_questions{
  width: 80%;
  text-align: left;
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;

  border: 1px dashed #ddd;
  box-shadow: 0 0 0 3px #fff, 0 0 0 5px #ddd, 0 0 0 10px #fff, 0 0 2px 10px #eee;
}

.role_question {
  display: block;
  text-align: left;
}

.role_question_text {
  font-family: Helvetica, Arial, Sans-Serif;
}

#test-result {
  padding: 20px;
}

.test-result-text {
  padding-top: 20px;
  padding-left: 20%;
  text-align: left;
}

</style>



